import React from "react";
import "./App.css";

import Menu from "./Pages/Menu/Menu";
import First from "./Pages/First/First";

export default class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      activeComponent: 0,
      userName: "",
      answers: []
    };
  }

  handleActiveScreen = () => {
    const { activeComponent, userName } = this.state;

    switch (activeComponent) {
      case 0:
        return (
          <Menu
            startInfographic={() => this.next()}
            setName={this.setName}
            name={userName}
          />
        );
      case 1:
        return <First user={userName} />;
    }
  };

  setName = value => {
    this.setState({ userName: value });
  };

  next = () => {
    this.setState(prevState => ({
      activeComponent: prevState.activeComponent + 1
    }));
  };

  back = () => {
    this.setState(prevState => ({
      activeComponent: prevState.activeComponent - 1
    }));
  };

  render() {
    return <>{this.handleActiveScreen()}</>;
  }
}
